@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;531&family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;531&family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  width: 100%;
  height: 100%;
}


.form-login{
  width: 100%;
  display: flex;
  flex-direction: column;
}

.label-login{
  padding-left: 10%;
  height: 40px;
}

.input-login{
  height: 35px;
  border-radius: 4px;
  width: 90%;
  border: none;
}

.button-login{
  width: 40%;
  height: 30px;
  margin-top: 30px;
  align-self: center;
  background-color: #E3B15A;
  border: none;
  border-radius: 4px;
  font-size: 17px;
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  color: #1E2044;
}

@media only screen and (min-width: 900px) {
  .input-login{
    width: 20%;
  }
  .button-login{
    width: 10%;
  }
  .label-login{
    width: 100%;
    text-align: center;
    padding-left: 0px;
  }
}
.main-home {
  width: 100%;
  height: 100%;
  background-color: #1E2044;
  font-family: 'Roboto Slab', serif;
}

.container-home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-home {
  align-items: center;
  width: 35%;
  margin-bottom: 10px;
  margin-top: 120px;
}

.gold {
  color: #E3B15A;
  text-align: center;
  margin-bottom: 30px;
  font-size: 25px;
}

.section-cpf {
  display: flex;
  align-self: flex-start;
  width: 100%;
  padding-left: 10%;
}

.cpf-cnpj {
  color: #E3B15A;
  font-size: 12px;
}

@media only screen and (min-width: 900px) {
  .section-cpf{
    padding-left: 0px;
    justify-content: center;
  }
}
.account-info{
  align-self: flex-end;
  font-size: 18px;
  /* margin-right: 10px; */
  font-family: 'Roboto Slab', serif; 
  
}
.back{
  height:3vh;
  width: 33.33vw;
}
.client-info{
  width: 100vw;
  color:#E3B15A;
  display: flex;
  justify-content: space-around;
  height: 3vh;
  font-family: Roboto Slab;

}
.client-account{
  margin: revert;
  font-size: 14px;
}
.client-name{
  color:#E3B15A;
  align-self:end;
  height: 27px;
  font-size: 24px;
  font-family: Roboto Slab;
}
.header{
  background: #1E2044;
  position: fixed;
  top: 0px;
  height: 14vh;
  width: 100vw;
 
}
.images-header{
  width: 100vw;
  place-items: baseline;
  display: flex;
  justify-content: space-between;
}
.img-back{
  height: 2.5vh;
  padding-left: 2vw;
}
.img-logo{
  padding-top: 1vh;
}
.logo{ 
  text-align: center;  
  width: 33.33vw;
}
.logo-image{
  fill:#E3B15A
}
.logo-logout{
  display: flex;
  width: 100vw;
  height: 52px;
  border-bottom: 2px solid #00003c44;
}
.logout{  
  text-align: end;
  width: 33.33vw;
  cursor: pointer;
}
.logout-image{
  padding-right: 5vw;;
  height:3vh;
}
.name-li{
  align-self: flex-end;
}

.visibility {
  visibility: hidden;
  width: 33.33vw;
}


@media(min-width: 900px){
  .client-name{
    font-size: 35px;
  }
  .client-account{
    font-size: 19px;
  }
  .header{
    height: 18vh;
  }
  .img-logo{
    height:11vh;
  }
  .logout-image {
    padding-right: 2vw;
    height: 5vh;
}
 .img-back {
  height: 4vh;
}
