.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 80px;
}

.div-extract {
  color: #1E2044;
  width: 85%;
  padding: 10px;
  box-shadow: 1px 2px 4px 2px rgba(30, 32, 68, 0.5);
  border-radius: 8px;
}

.p-extract {
  margin: 0px;
  overflow-wrap: break-word;
}

.h-extract {
  font-family: Montserrat;
  color: #1E2044;
  text-align: center;
  margin-top: 21vh;
}

.p-margin {
  margin-top: 10px;
}

.p-bold {
  font-weight: bold;
}

.p-size {
  font-size: 40px;
  margin-bottom: 20px;
}

.text-center {
  text-align: center;
}

.see-statement{
  text-align: center;
  background-color: #1E2044;
  color: #E3B15A
}

.icon-card{
  width: 30%;
}

.span-date-value{
  display: flex;
  justify-content: space-between;
}

@media only screen and (min-width: 900px) {
  .div-extract{
    width: 30%;
  }
}
.footer-images{
  height: 72%;
}
.footer{
  background:#1E2044;
  height: 8vh;
